import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

import Home from "./screen/HomeScreen"; 
import Portafolio from "./screen/PortafolioScreen"; 
import Contacto from "./screen/ContactoScreen"; 

const NavBar = () => {
    return (
        <Router>
       <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <Link className="navbar-brand" to="/">Navbar</Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div className="navbar-nav">
        <Link className="nav-link active" to="/">Home <span className="sr-only">(current)</span></Link>
        <Link className="nav-link" to="/Portafolio">Portafolio</Link>
        <Link className="nav-link" to="/Contacto">Contacto</Link>
        <Link className="nav-link disabled" to="/galeria" tabindex="-1" aria-disabled="true">Galeria</Link>
        </div>
        </div>
        
        </nav>
          <hr />

          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route exact path="/Portafolio">
              <Portafolio />
            </Route>
            <Route exact path="/Contacto">
              <Contacto />
            </Route>
          </Switch>
        
      </Router>
   

      
    

    )
}

export default NavBar
