import React from 'react'
import { useQuery, gql } from '@apollo/client';

const tecnology = gql`
{
    Technology{
      _id
      nameSkill
      description
    }
  }
`;

function ExchangeTecnology() {
    const { loading, error, data } = useQuery(tecnology);
  
    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error :(</p>;
  
    return data.Technology.map(({ nameSkill, description,_id }) => (
      <div key={_id}>
        <p>
          {nameSkill}: {description}
        </p>
      </div>
    ));
  }
const PortafolioScreen = () => {
    return (
        <ExchangeTecnology />
    )
}

export default PortafolioScreen
